<template>
	<div>
		<div to="/Zlfy/Costintro" class="Block">
			{{title}}
		</div>
		<div class="libeGray"></div>
		<div class="row">
			<div v-for="item in items" class="col-lg-6">
				<a href="#">
					{{item}}
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	var datas = [
		["<<>>", "<<>>", "<<>>", "<<PCT>>"],
		["<<>>", "<<>>"],
		["<<>>", "<<>>"]
	]
	export default {
		props: ['frome', 'title'],
		name: 'download',
		data() {
			return {
				items: {}
			}
		},
		mounted() {
			if(this.frome == "Zlfy") {
				this.items = datas[0];
			} else if(this.frome == "Zlzz") {
				this.items = datas[1];
			} else {
				this.items = datas[2];
			}
		}
	};
</script>

<style scoped>
	.Block {
		line-height: 3em;
		color: #666666;
		font-size: larger;
		text-align: center;
		display: inline-block;
	}
	
	.libeGray {
		height: 1px;
		background-color: #E6E6E6;
	}
	
	.row {
		line-height: 2em;
		padding: 20px;
		font-size: medium;
	}
</style>


